.selected-expansion-panel-wrapper {
  height: calc(100vh - 320px);
  overflow-y: scroll;
  // display: inline-block;
  width: 100%;

  .edit-program-list {
    padding: 2px;

    .exer-expansion-panel {
      padding: 0;
      pointer-events: auto;
      // margin-left: -16px !important;
      cursor: auto;
      // border-bottom: solid 1px #ccc !important;
      // box-shadow: none !important;

      &:hover {
        .expansion-panel-handle {
          mat-icon {
            visibility: visible;
          }
        }
      }

      .exer-expansion-panel-header {
        padding-left: 46px;

        mat-panel-title {
          width: 120px;
          overflow: hidden;
          margin-right: 32px;
          margin-left: 16px;
          // align-items: flex-start;

          .title-name {
            // padding-top: 16px;
          }
        }

        .panel-description {
          padding-right: 8px;

          .session-overview {
            display: flex;
            flex-wrap: wrap;
            gap: 0px;
            flex: 1;

            & div {
              flex: 1 0 45%; /* Adjusts the width of each item */
              box-sizing: border-box; /* Ensures padding/margin is included in width */
            }
          }

          .options-wrapper {
            // color: black;
            align-self: center;
            padding-top: 4px;
          }
        }
      }

      .panel-content {
        display: flex;
        gap: 16px;
        padding-top: 16px;
        padding-left: 30px;
        justify-content: space-evenly;

        // & * {
        //   flex-grow: 1;
        // }

        .name-description-label {
          display: flex;
          flex-direction: column;
          gap: 16px;
          // width: 30%;
          flex: 3;

          .mat-mdc-form-field-subscript-wrapper {
            height: 0;
          }

          .description-text-area {
            // width: 100%;
          }

          // span {
          //   color: rgba(0, 0, 0, 0.6);
          //   margin-bottom: 8px;
          // }
        }

        .exercise-session {
          flex: 1;
          display: flex;
          flex-direction: column;
          gap: 16px;

          .panel-input {
            .mat-mdc-form-field-subscript-wrapper {
              height: 0;
            }
          }
        }
      }

      .expansion-panel-handle {
        position: absolute;
        top: 28px;
        left: 0px;
        cursor: move;
        // color: #ccc;
        width: 28px;
        height: 28px;
        padding: 5px;
        margin-left: 8px;
        display: flex;
        align-items: center;

        .handle-number {
          margin-right: 10px;
          margin-top: -5px;
        }

        mat-icon {
          visibility: hidden;
        }
      }
    }
  }
}

